import React, { useState } from 'react'
import Link from 'gatsby-link'
import { globalHistory as history } from '@reach/router'
import { useMediaQuery } from 'react-responsive'

import { locationClassName } from './../helpers/History.helper'
import AppContext from '../context/AppContext'

export function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const isDesktop = useMediaQuery({
    query: '(min-device-width: 1224px)',
  })
  const isMobileOrTablet = useMediaQuery({
    query: '(max-width: 1224px)',
  })
  const nameDOM = (
    <li className="edrig__header_name">
      <Link to="/">
        <span>Edouard<br />Rigaudiere</span>
        <small>film composer</small>
      </Link>
    </li>
  )

  const linksDOM = (
    <>
      { (history.location.pathname !== '/' && isMobileOrTablet) ? nameDOM : <></> }
      <AppContext.Consumer>
        {ctx => (
          <>
            <li className={ locationClassName('work') }><Link to="/work/" onClick={ () => ctx.setCurrentWork(undefined) }>Work</Link></li>
            <li className={ locationClassName('music') }><Link to="/music/">Music</Link></li>
            <li className={ locationClassName('about') }><Link to="/about/">About/Contact</Link></li>
          </>
        )}
      </AppContext.Consumer>
    </>
  )

  return (
    <header className="edrig__header">
      {
        isDesktop &&
        <nav>
          <ul className="edrig__header_list">
            { nameDOM }
            { linksDOM }
          </ul>
        </nav>
      }
      {
        isMobileOrTablet &&
        <nav className="nav-mobile">
          <div className="edrig__header_burgermenu">
            {
              isMenuOpen ?
              <img src="/images/menuclose.svg" alt="Close Menu" onClick={() => setIsMenuOpen(false)}/> :
              <img src="/images/menu.svg" alt="Menu" onClick={() => setIsMenuOpen(true)}/>
            }
          </div>
          <ul className={`edrig__header_list ${ isMenuOpen ? 'open' : '' }`}>
            { isMenuOpen ?
              linksDOM :
              nameDOM
            }
          </ul>
        </nav>
      }
    </header>
  )
}
